import { isObject, isArray, isEmptyObject } from '../helper/util';
import { ObjectInterface, FunctionInterface, CallbackInterface } from '../types';
/**
 * Ê∑±Â∫¶ÈÅçÂéÜÂØπË±°, Â∞ÜÂ§öÂ±ÇÂØπË±°ÊâÅÂπ≥Âåñ
 * @param { object } obj ÈÅçÂéÜÂØπË±°
 * @param { function } fn ÊØè‰∏Ä‰∏™keyÊâßË°åÁöÑÊñπÊ≥ï
 * **/ 
function objectEach(obj: ObjectInterface, fn: FunctionInterface) {
  // Â¶ÇÊûú‰∏çÊòØÂØπË±°Áõ¥Êé•ËøîÂõû
    if (!isObject(obj)) {
      throw Error('Parameter must be object');
    }
    const keys: string[] = Object.keys(obj);
    keys.forEach(key => {
      const value  = obj[key];
     if (isObject(value) && value) {
      objectEach(value, fn);
     } else {
      fn(value, key);
     }
    });
};

/**
 * ÂØπË±°Ê∑±Êã∑Ë¥ù
 * @param { object } target ÈúÄË¶ÅÊã∑Ë¥ùÁöÑÂØπË±°
 * */

function objectClone(target: ObjectInterface): ObjectInterface {
  const result: ObjectInterface = target.constructor === Array ? [] : {};
  for (let key in target) {
    if (target.hasOwnProperty(key)) {
      if (target[key] && isObject(target[key])) {
        result[key] = target[key].constructor === Array ? [] : {};
        result[key] = objectClone(target[key]);
      } else {
        result[key] = target[key];
      }
    }
  }
  return result;
}
/**
* ÂØπË±°ÊØîËæÉ(‰∏çÊØîËæÉÂéüÂûãÈìæÁöÑÂ±ûÊÄß)
* @param original ÂéüÂßãÂØπË±°
* @param target ÁõÆÊ†áÂØπË±°
 * 
 * **/
function objectDiff(original: ObjectInterface, target: ObjectInterface): ObjectInterface { 

  const result: ObjectInterface = {};

  const targetKeys: string[] = Object.keys(target);

targetKeys.forEach(key => {

  // Êñ∞Â¢ûÁöÑÂ±ûÊÄß
  if (!original[key]) {
    result[key] = target[key];
    return;
  }

  // Â±ûÊÄßÊòØ‰∏™ÂØπË±°
  if (isObject(target[key])) {
    let values = objectDiff(original[key] || {}, target[key]);
    if (isEmptyObject(values)) {
      result[key] = values;
    }
    return;
  }

  // Â±ûÊÄßÊòØ‰∏™Êï∞ÁªÑ
  if (isArray(target[key])) {
    result[key] = target[key];
    return;
  }

  // Â±ûÊÄßÂÄºÊõ¥Êñ∞‰∫Ü
  if (original[key] !== target[key]) {
    result[key] = target[key];
  }
});

return result;

}

/**
 * Â∞ÜÂØπË±°ËΩ¨‰∏∫Â≠óÁ¨¶‰∏≤
 * @param { object } obj ÈúÄË¶ÅËΩ¨ÂåñÁöÑÂØπË±°
*/
function objectToQueryString(obj: ObjectInterface): string {
  return obj
    ? Object.entries(obj).reduce((queryString, [key, val]) => {
      const symbol = queryString.length === 0 ? '' : '&';
      if (isObject(val) || isArray(val)) {
        queryString += val ? `${symbol}${key}=${JSON.stringify(val)}` : '';
      } else {
        queryString += val ? `${symbol}${key}=${val}` : '';
      }
      return queryString;
    }, '')
    : '';
}

 /**
  *  Â∞ÜÂØπË±°ËΩ¨Âåñ‰∏∫key+value Â≠óÁ¨¶‰∏≤
  * @param { object } obj ÈúÄË¶ÅËΩ¨ÂåñÁöÑobject
  * @param { string } separator ÂàÜÈöîÁ¨¶Âè∑
  * @param { Function } callback Ëá™ÂÆö‰πâÂ§ÑÁêÜÂõûË∞ÉÂáΩÊï∞
  */

function objectToString(obj: ObjectInterface, separator?: string, callback?:CallbackInterface<[ string, any], void>) {
    let queryStr = '';
    for (const [key, value] of Object.entries(obj)) {
      if (callback) {
        callback(key, value);
      } else {
        queryStr +=`${key}: ${value}${separator || ';'}`;
      }
    }
     return queryStr;
  }

  export { objectDiff, objectClone, objectToString, objectEach }