import { FunctionInterface, ObjectInterface } from './../types/index';
// ÂáΩÊï∞ÊâßË°åÁõ∏ÂÖ≥

/**
 * ÂáΩÊï∞Èò≤Êäñ (Âè™ÊâßË°åÊúÄÂêé‰∏ÄÊ¨°ÁÇπÂáª)
 * @param { function } fn  ÊâßË°åÁöÑÂáΩÊï∞
 * @param { number } time Âª∂ËøüÊâßË°åÊó∂Èó¥
 */
function debounce(fn: FunctionInterface, time?: number) {
  let delay: number = time || 500;
  let timer:any  = null;
  return function() {
    const that = this;
    let args = arguments;
    if (timer) {
      clearTimeout(timer);
    }
    timer = setTimeout(() => {
      timer = null;
      fn.apply(that, args);
    }, delay);
  }
}

/**
 *  ÂáΩÊï∞ËäÇÊµÅ
 * @param { function } fn  ÊâßË°åÁöÑÂáΩÊï∞
 * @param { number } time Âª∂ËøüÊâßË°åÊó∂Èó¥
 * 
 */

function throttle(fn: ObjectInterface, time?: number) {
  let last: number = 0;
  let timer: any = null;
  let interval = time || 500;
  return function () {
    const that = this;
      let args = arguments;
      let now: number = +new Date();
      if (last && now - last < interval) {
          clearTimeout(timer);
          timer = setTimeout(() => {
              last = now;
              fn.apply(that, args);
          }, interval);
      } else {
          last = now;
          fn.apply(that, args);
      }
  }

}

export { debounce, throttle }